:root {
    --blue-green-100: linear-gradient(90deg, hsla(160, 100%, 97%, 1.0), hsla(160, 100%, 65%, 1.0));
    --blue-green-200: linear-gradient(90deg, hsla(160, 100%, 90%, 1.0), hsla(160, 100%, 50%, 1.0));
    --blue-green-300: linear-gradient(90deg, hsla(160, 100%, 80%, 1.0), hsla(160, 100%, 45%, 1.0));
    --blue-green-400: linear-gradient(90deg, hsla(160, 100%, 70%, 1.0), hsla(160, 100%, 40%, 1.0));
    --blue-green-500: linear-gradient(90deg, hsla(160, 100%, 60%, 1.0), hsla(160, 100%, 35%, 1.0));
    --blue-green-600: linear-gradient(90deg, hsla(160, 100%, 50%, 1.0), hsla(160, 100%, 30%, 1.0));
    --blue-green-700: linear-gradient(90deg, hsla(160, 100%, 40%, 1.0), hsla(160, 100%, 25%, 1.0));
    --blue-green-800: linear-gradient(90deg, hsla(160, 100%, 30%, 1.0), hsla(160, 100%, 20%, 1.0));
    --blue-green-900: linear-gradient(90deg, hsla(160, 100%, 20%, 1.0), hsla(160, 100%, 15%, 1.0));

    --green-blue-100: linear-gradient(90deg,hsla(160, 100%, 65%), hsl(160, 100%, 97%));
    --green-blue-200: linear-gradient(90deg, hsla(160, 100%, 50%), hsl(160, 100%, 90%));
    --green-blue-300: linear-gradient(90deg, hsla(160, 100%, 45%), hsl(160, 100%, 80%));
    --green-blue-400: linear-gradient(90deg, hsla(160, 100%, 40%), hsl(160, 100%, 70%));
    --green-blue-500: linear-gradient(90deg, hsla(160, 100%, 35%), hsl(160, 100, 60%));
    --green-blue-600: linear-gradient(90deg, hsla(160, 100%, 30%), hsl(160, 100, 50%));
    --green-blue-700: linear-gradient(90deg, hsla(160, 100%, 25%), hsl(160, 100%, 40%));
    --green-blue-800: linear-gradient(90deg, hsla(160, 100%, 20%), hsl(160, 100%, 30%));
    --green-blue-900: linear-gradient(90deg, hsla(160, 100%, 15%), hsl(160, 100%, 20%));

}

